<template>
    <div id="common-box">
        <div class="common-form">
            <h4>考试信息</h4>

            <div class="handle-box">
                <el-input placeholder="查询关键字" v-model="keyword"></el-input>
                <el-select v-model="typeStatus">
                <el-option v-for="item in typelist"
                        :key="item.label"
                        :label="item.label"
                        :value="item.value"></el-option>
                </el-select>
                <el-button @click="handleSearch">搜索</el-button>
                <el-button @click="handleCancel" v-show="isUse">取消</el-button>
            </div>
            
            <div class="test-form">
                <el-table
                    height="420"
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column align="center" label="考试全称" prop="examName"></el-table-column>
                    <el-table-column align="center" label="考试类型" prop="examType"></el-table-column>     
                    <el-table-column align="center" label="考试等级" prop="examGrade"></el-table-column>     
                    <el-table-column align="center" label="开始报名时间" prop="startTime"></el-table-column>     
                    <el-table-column align="center" label="截止报名时间" prop="deadlineTime"></el-table-column>     
                    <el-table-column align="center" label="考试当天日期" prop="examDate"></el-table-column>     
                    <el-table-column align="center" label="考生报到时间段" prop="arriveTime"></el-table-column>
                    <el-table-column align="center" label="笔试时间段" prop="examTime"></el-table-column>     
                    <el-table-column align="center" label="准考证开放打印时间" prop="printTime"></el-table-column>     
                    <el-table-column align="center" label="操作">
                        <template slot-scope="scope">
                            <el-button type="success" size="small" @click="handleEdit(scope.row)">报名</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 底部导航 -->
            <div class="pagination">
                <Pagination
                :totalMsg="query.totalMsg"
                :pageIndex="query.pageIndex"
                :pageSize="query.pageSize"
                @setPageIndex="getPageIndex"
                @setPageSize="getPageSize"></Pagination>
            </div>
        </div>
    </div>
</template>

<script>
import Pagination from '@/components/Pagination.vue'

export default {
    components:{
        Pagination
    },
    data(){
        return {
            isUse:false,
            typeStatus:'',
            typelist:[
                { label:'学位英语',value:'学位英语'},
                { label:'计算机',value:'计算机'}
            ],
            keyword:'',
            exams:{},
            tableData:[],
            user:{},
            query:{
                pageSize:5,
                pageIndex:1,
                totalMsg:null,
            }
        }
    },
    methods:{
        getPageSize(val){
            this.query.pageSize=val;
            this.getExamList();
        },
        getPageIndex(val){
            this.query.pageIndex=val;
            this.getExamList();
        },
        handleSearch(){
            if(!this.typeStatus || !this.keyword){
                return this.$message.error('请输入要查询的项');
            }
            this.isUse=true;
            this.getExamList();
        },
        handleCancel(){
            this.isUse=false;
            this.typeStatus='';
            this.keyword='';
            this.getExamList();
        },
        handleEdit(info){//报名
            let that=this;
            this.$api.signexam(
                that.user.id,
                info.id
            ).then(res=>{
                if(res.err_code === 100){
                    that.$message.success(res.err_msg);
                }else{
                    that.$message.error(res.err_msg);
                }
            })
        },
        getExamList(){//获取考试信息
            let that=this;
            this.$api.stuexam({
                type:that.typeStatus,
                keyword:that.keyword,
                pageNum:1,
                pageSize:10
            }).then(res=>{
                that.tableData=res.data.exams.list;
                that.query.totalMsg=res.data.exams.total
            })
        },
        getUserInfo(){
            let that=this;
            this.$api.studentinfo().then(res=>{
                that.user=res.data.info
            })
        }
    },
    created(){
        this.getUserInfo();
        this.getExamList();
    }
}
</script>

<style lang="scss" scoped>
    .handle-box{
        display: flex;
        margin: 10px 0;
        .el-input{
            width: 200px;
            margin: 0 10px;
        }
        .el-button{
            margin: 0 10px;
        }
    }
</style>